import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-live-royal-wedding-2018',
  templateUrl: './live-royal-wedding-2018.component.html',
  styleUrls: ['./live-royal-wedding-2018.component.css']
})
export class LiveRoyalWedding2018Component implements OnInit {

  constructor() { }

  ngOnInit() {
  }
  stationName = "The Royal Wedding 2018 LIVE"
  description = "On Monday 27 November 2017 The Prince of Wales announced the engagement of his son Prince Harry to Ms. Meghan Markle. The wedding will take place at St George's Chapel, Windsor on Saturday 19 May. The service will begin at St George's Chapel at midday and will be conducted by the Dean of Windsor, The Rt Revd. David Conner. The Most Revd. and Rt Hon. Justin Welby, Archbishop of Canterbury, will officiate as the couple make their marriage vows. At 1pm, following the service, the couple will travel around Windsor in a horse-drawn carriage, providing an opportunity for members of the public to see them and join in with the celebrations, and you can watch the wedding LIVE on WorldTelevision. Meghan Markle has asked the Prince of Wales to accompany her down part of the aisle on her wedding day to Prince Harry, as she opts not to be given away. The US star, who this week learned that her own father would not make the wedding due to ill health, will walk the first half of the St George's Chapel aisle alone, surrounded by young page boys and bridesmaids but without anyone by her side. At the Quire, which effectively marks the second half of the aisle, Ms. Markle will be met by the Prince of Wales, her future father-in-law, who will take her arm for the final steps. The ceremony will omit the traditional moment of 'giving away', where the presiding Archbishop asks: 'Who giveth this woman to be married to this man?'"
}



